.MainMenu {
  position: relative;
  width: 180px;
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  box-sizing: border-box;
  transition-duration: .1s;
} 
.MainMenu:hover,
.MainMenu.active {
  background-color: #0E5547;
  border-bottom: 3px solid #003D31;
  font-size: 1.02em;
}